body {
  min-width: 320px;
  min-height: 100vh;
  margin: 0;
  padding-bottom: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  color: rgba(17, 17, 17, 0.56);
  background-repeat: no-repeat;
  background-position: 50% 0;
  background-color: #FFFFFF; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

*,
*::before,
*::after {
  box-sizing: border-box; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page {
  width: 100%; }

.page__body {
  min-height: 100vh;
  background-size: contain;
  background-image: url("../img/main-image-desktop.jpg");
  background-position: top 0 left 100%;
  background-repeat: no-repeat; }
  @media (min-resolution: 2dppx) {
    .page__body {
      background-image: url("../img/main-image-desktop-2x.jpg"); } }
  @media (max-width: 1365px) {
    .page__body {
      background-image: url("../img/main-image-pre-desktop.jpg"); } }
  @media (max-width: 1365px) and (min-resolution: 2dppx) {
    .page__body {
      background-image: url("../img/main-image-pre-desktop-2x.jpg"); } }
  @media (max-width: 959px) {
    .page__body {
      background-image: none; } }

.page__main {
  width: 1366px;
  margin: 0 auto; }
  @media (max-width: 1365px) {
    .page__main {
      width: 960px;
      box-shadow: none; } }
  @media (max-width: 959px) {
    .page__main {
      width: 640px;
      display: flex;
      flex-direction: column; } }
  @media (max-width: 639px) {
    .page__main {
      width: 100%; } }

.page__main-image {
  display: none; }
  @media (max-width: 959px) {
    .page__main-image {
      background-color: #938C83;
      display: block;
      width: 100%;
      height: 100%; } }

.form__list {
  list-style: none;
  padding: 0; }

.form {
  z-index: 2;
  width: auto;
  height: 100vh;
  padding: 127px 121px 128px 132px;
  min-width: 600px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-right: 682px;
  background-color: #FFFFFF; }
  @media (max-width: 1365px) {
    .form {
      padding: 119px 52px 119px 68px;
      min-width: 480px;
      margin-right: 480px; } }
  @media (max-width: 959px) {
    .form {
      padding: 64px 44px;
      order: 2;
      margin-right: 0;
      height: 100%; } }
  @media (max-width: 639px) {
    .form {
      padding: 33px 16px 64px;
      min-width: 320px; } }

.form h1 {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 40px;
  line-height: 48px;
  color: #111111;
  margin: 0 0 23px; }
  @media (max-width: 1365px) {
    .form h1 {
      font-size: 32px;
      line-height: 40px;
      margin: 0 0 20px; } }
  @media (max-width: 959px) {
    .form h1 {
      margin-bottom: 16px; } }
  @media (max-width: 639px) {
    .form h1 {
      font-size: 24px;
      line-height: 32px;
      padding-right: 20px; } }

.form p {
  margin: 0 0 6px; }
  @media (max-width: 1365px) {
    .form p {
      margin: 0 0 2px; } }
  @media (max-width: 959px) {
    .form p {
      font-size: 14px;
      line-height: 20px;
      padding-right: 40px; } }
  @media (max-width: 639px) {
    .form p {
      padding-right: 0;
      margin-bottom: 4px; } }

.form__element {
  margin-bottom: 17px; }
  @media (max-width: 639px) {
    .form__element {
      margin-bottom: 7px; } }

.form__element:hover {
  background-color: #C4C4C4; }

.form__element input {
  width: 100%;
  border: none;
  border-bottom: 1px solid rgba(17, 17, 17, 0.24);
  padding: 13px 0;
  color: rgba(17, 17, 17, 0.56);
  position: relative;
  background-color: transparent; }
  @media (max-width: 639px) {
    .form__element input {
      padding: 3px 0 3px;
      font-size: 14px;
      line-height: 20px; } }

@media (max-width: 639px) {
  .form__element input::placeholder {
    opacity: 0; } }

.form__element input:focus, .form__element input:active, .form__element input:hover {
  outline: none;
  color: #111111;
  border-bottom: 1px solid transparent; }

.form__element input:focus-within {
  background-color: #C4C4C4; }

@media (max-width: 639px) {
  .form__element input::before {
    content: "Имя";
    position: absolute;
    top: 0;
    left: 0;
    font-size: 12px;
    line-height: 20px;
    font-size: 12px;
    line-height: 20px;
    color: #111111;
    height: 40px;
    width: 20px; } }

@media (max-width: 639px) {
  .form__element label {
    position: relative;
    width: auto;
    height: auto;
    margin: 0;
    border: none;
    padding: 0;
    white-space: nowrap;
    clip-path: none;
    clip: auto;
    overflow: visible;
    font-size: 12px;
    line-height: 20px; } }

.form__checkbox {
  padding-left: 24px;
  margin-bottom: 23px; }
  @media (max-width: 639px) {
    .form__checkbox {
      margin-bottom: 18px; } }

.form__checkbox label {
  position: relative;
  color: #111111; }
  @media (max-width: 639px) {
    .form__checkbox label {
      font-size: 14px;
      line-height: 20px; } }

.form__checkbox input + label::before {
  content: "";
  position: absolute;
  top: 2px;
  left: -24px;
  width: 16px;
  height: 16px;
  border-radius: 0;
  border: 1px solid #111111;
  opacity: 0.32; }

.form__checkbox input:checked + label::before {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url("../img/svg/checkbox.svg");
  opacity: 0.32; }

.form__checkbox input:focus + label:before {
  outline: thin dotted;
  outline: 5px auto #111111;
  opacity: 1; }

.form__checkbox:hover input:checked + label::before, .form__checkbox:hover input + label::before, .form__checkbox:active input + label::before {
  opacity: 1; }

.form__button {
  text-align: center;
  background-color: #FFFFFF;
  border: 2px solid rgba(17, 17, 17, 0.16);
  border-radius: 842.105px;
  padding: 13px 23px;
  width: max-content;
  margin-bottom: 32px; }
  @media (max-width: 959px) {
    .form__button {
      margin-bottom: 16px; } }
  @media (max-width: 639px) {
    .form__button {
      font-size: 14px;
