  background-color: #C4C4C4; }

.form__button:active {
  color: rgba(17, 17, 17, 0.56); }

.form__confidentiality {
  font-size: 10px;
  line-height: 16px;
  padding-right: 20px; }
  @media (max-width: 1365px) {
    .form__confidentiality {
      padding-right: 40px; } }
  @media (max-width: 959px) {
    .form__confidentiality {
      padding-right: 0px; } }
  @media (max-width: 639px) {
    .form__confidentiality {
      padding-right: 40px; } }

.form__confidentiality a {
  color: rgba(17, 17, 17, 0.56);
  border-bottom: 1px solid rgba(17, 17, 17, 0.56); }

.form__confidentiality a:focus, .form__confidentiality a:hover {
  outline: none;
  color: #111111;
  border-bottom: 1px solid #111111; }

.form__confidentiality a:active {
  outline: none;
  color: rgba(17, 17, 17, 0.24);
  border-bottom: 1px solid rgba(17, 17, 17, 0.24); }
